.go-to-edit-btn {
    cursor: pointer;
    font-size: 30px;
    padding: 30px;
    border: none;
    border-radius: 10px;
    transition: 0.3s linear;
    position: absolute;
    top: 250px;
    right: 200px;
    background-color: lightcoral;
}

.go-to-edit-btn:hover {
    background-color: rgb(241, 76, 76);
}